<template>
  <a-layout>
    <a-layout-header :style="{ background: '#EFEFEF', padding:'0 10px', height:'7vh'}" >
      <span class="FPC-header-text">设备信息  </span>
    </a-layout-header>
    <a-layout-content> 
    <div style="position: relative; top: 70px">
      <div :style="{position:'absolute', backgroundColor: '#56E67E', height: '10px', width:'20%'}" class="oui-line-1">
          <div class="oui-line-active" :style="{height: 8 + 'px'}"/>
      </div>
      <div :style="{position:'absolute', backgroundColor: '#56E67E', height: '10px', width:'20%'}" class="oui-line-2">
          <div class="oui-line-active" :style="{height: 8 + 'px'}"/>
      </div>
    </div>
    <div :style="{ padding: '24px', background: '#fff', minHeight: '90vh'}">
      <div>
        <div @click="tab = 'devices'" style="display:inline;" class="tab-tilte">
          <img src="../assets/devices.png"/>
          <div class='tab-tilte-div' :class="{active: tab === 'devices'}">终端设备(0)</div>
        </div>
        <div @click="tab = 'router'" style="display:inline;" class="tab-tilte">
          <img src="../assets/router.png"/>
          <div class='tab-tilte-div' :class="{active: tab === 'router'}">系统</div>
        </div>
        <div @click="tab = 'internet'" style="display:inline;" class="tab-tilte">
          <img src="../assets/internet.png"/>
          <div class='tab-tilte-div' :class="{active: tab === 'internet'}">WAN</div>
        </div>
      </div>
      <div style="clear: both">
        <div v-show="tab === 'devices'">
          <a-card title="在线设备" style="margin-bottom: 20px">
            <a-table :columns="devicesColumns" :data-source="devices"/>
          </a-card>
        </div>
        <div v-show="tab === 'router'">
          <div style="display: flex; justify-content: space-around;">
            <oui-dashboard :percent="cpuPercentage" :label="cpuLabele"/>
            <oui-dashboard :percent="memPercentage" :label="memLabele"/>
            <oui-dashboard :percent="storagePercentage" :label="storageLabele"/>
          </div>
          <a-descriptions title="系统信息" :column="1" bordered>
              <a-descriptions-item v-for="item in sysinfo" :key="item[0]" :label="item[0]">{{ item[1] }}</a-descriptions-item>
          </a-descriptions>
        </div>
        <div v-show="tab === 'internet'">
          <a-descriptions title="网络" :column="1" bordered>
            <a-descriptions-item v-for="item in waninfo" :key="item[0]" :label="item[0]">{{ item[1] }}</a-descriptions-item>
          </a-descriptions>
        </div>
    </div>
    </div> 
    </a-layout-content>
    <!-- <a-layout-footer :style="{textAlign: 'center', height: '3vh'}">
      Ant Design ©2018 Created by Ant UED
    </a-layout-footer> -->
  </a-layout>
</template>

<script>
export default {
  data(){
    return{
       tab: 'router',
       cpuLabele:'CPU使用率',
       memLabele:'内存使用率',
       storageLabele:'存储',
       cpuPercentage: 65,
       memPercentage: 32,
       storagePercentage: 8,
       sysinfo: [
          ['主机名', 'OpenWrt'],
          ['主机型号', 'Xiaomi MiWiFi Mini'],
          ['架构', 'MediaTek MT7620A ver2 eco5'],
          ['固件版本', 'r14001-9950tx92e3'],
          ['内核版本', '5.4.52'],
          ['本地时间', new Date().toString()],
          ['运行时间', '0']
       ],
       devicesColumns: [
        { dataIndex: 'hostname', title: '主机名' },
        { dataIndex: 'ipaddr', title: 'IPv4-地址' },
        { dataIndex: 'macaddr', title: 'MAC-地址'}
      ],
      devices: [],
      waninfo: [
        ['IP地址', '192.168.0.2'],
        ['网关', '192.168.0.1'],
        ['DNS', '10.10.10.10']
      ],

    }
  }
}
</script>

<style>
.FPC-header-text{
  font-size: 20px;
  font-weight: 600;
}
.tab-tilte {
  float: left;
  width: calc(100% / 3);
  padding: 10px 0;
  text-align: center;
  cursor: pointer;
}
.tab-tilte-div{
  background-color:#e8e8e8
}
.tab-tilte:first-child div{
  border-radius: 20px 0 0 0;
}
.tab-tilte:last-child div{
  border-radius: 0 20px 0 0;
}
.oui-line-1{
  text-align: center;
  margin:  5vh 0 0 23%;
}

.oui-line-2{
  text-align: center;
  margin:  5vh 0 0 56%;
}
.oui-line-active {
    position: relative;
    width: 10px;
    height: 5px;
    background: #09f;
    border-radius: 10px;
    animation: oui-line-active 4s ease-in-out infinite;
}
@keyframes oui-line-active {
    0% {
      left: 0;
    }
    50% {
      left: calc(100% - 10px);
    }
    100% {
      left: 0;
    }
  }
  
.active {
  background-color: #09f;
  color: #fff;
}
</style>