import Vue from "vue";
import VueRouter from "vue-router";
import login from "../views/login.vue";
import homePage from '../views/homePage.vue';
import firstPage from '../views/firstPage.vue';
import WANPage from '../views/WANPage.vue';
import _4GPage from '../views/4GPage.vue';
import WIFIPage from '../views/WIFIPage.vue';
import LANPage from '../views/LANPage.vue';
import joinRouter from '../views/joinRouter.vue';
import RS232Page from '../views/RS232Page.vue';
import RS485Page from '../views/RS485Page.vue';
import IOPage from '../views/IOPage.vue';
import uploadPage from '../views/uploadPage.vue';
import CNCBase from '../views/CNCBase.vue';
import CNCFile from '../views/CNCFile.vue';
import CNCZhou from '../views/CNCZhou.vue';
import CNCZDY from '../views/CNCZDY.vue';
import CNCZDYH from '../views/CNCZDYH.vue';
import CNCDJ from '../views/CNCDJ.vue';
import ApplicationPage from '../views/ApplicationPage.vue';
import userPage from '../views/userPage.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "login",
    component: login
  },
  {
    path: "/homePage",
    name: "homePage",
    component: homePage,
    children:[
      {
        path: 'firstPage',
        name: 'firstPage',
        component: firstPage
      },
      {
        path: 'WANPage',
        name: 'WANPage',
        component: WANPage
      },
      {
        path: 'WIFIPage',
        name: 'WIFIPage',
        component: WIFIPage
      },
      {
        path: '_4GPage',
        name: '_4GPage',
        component: _4GPage
      },
      {
        path: 'LANPage',
        name: 'LANPage',
        component: LANPage
      },
      {
        path: 'joinRouter',
        name: 'joinRouter',
        component: joinRouter
      },
      {
        path: 'RS232Page',
        name: 'RS232Page',
        component: RS232Page
      },
      {
        path: 'RS485Page',
        name: 'RS485Page',
        component: RS485Page
      },
      {
        path: 'IOPage',
        name: 'IOPage',
        component: IOPage
      },
      {
        path: 'uploadPage',
        name: 'uploadPage',
        component: uploadPage
      },
      {
        path: 'CNCBase',
        name: 'CNCBase',
        component: CNCBase
      },
      {
        path: 'CNCFile',
        name: 'CNCFile',
        component: CNCFile
      },
      {
        path: 'CNCZhou',
        name: 'CNCZhou',
        component: CNCZhou
      },
      {
        path: 'CNCZDY',
        name: 'CNCZDY',
        component: CNCZDY
      },
      {
        path: 'CNCZDYH',
        name: 'CNCZDYH',
        component: CNCZDYH
      },
      {
        path: 'CNCDJ',
        name: 'CNCDJ',
        component: CNCDJ
      },
      {
        path: 'ApplicationPage',
        name: 'ApplicationPage',
        component: ApplicationPage
      },
      {
        path: 'userPage',
        name: 'userPage',
        component: userPage
      }
    ]
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
