<template>
  <a-layout class="components-layout">
    <a-layout-sider
      breakpoint="lg"
      collapsed-width="0"
      @collapse="onCollapse"
      @breakpoint="onBreakpoint"
    >
      <div class="components-logo" />
      <a-menu theme="dark" mode="vertical" @select="handleClick" :default-selected-keys="['1']">
        <a-menu-item key="1">
          <a-icon type="user" />
          <span class="nav-text">设备信息</span>
        </a-menu-item>
        <a-sub-menu key="sub1">
        <span slot="title"><a-icon type="appstore" /><span>设备配置</span></span>
        <a-menu-item key="2">
          WLAN配置
        </a-menu-item>
        <a-menu-item key="3">
          4G配置
        </a-menu-item>
        <a-menu-item key="4">
          WIFI配置
        </a-menu-item>
        <a-menu-item key="5">
          LAN配置
        </a-menu-item>
        <a-menu-item key="6">
          桥接路由配置
        </a-menu-item>
        <a-menu-item key="7">
          RS232配置
        </a-menu-item>
        <a-menu-item key="8">
          RS485配置
        </a-menu-item>
        <a-menu-item key="9">
          IO配置
        </a-menu-item>
        <a-menu-item key="10">
          上报配置
        </a-menu-item>
      </a-sub-menu>
      <a-sub-menu key="sub2">
        <span slot="title"><a-icon type="setting" /><span>采集设备配置</span></span>
        <a-menu-item key="11">
          基本配置
        </a-menu-item>
        <a-menu-item key="12">
          文件传输
        </a-menu-item>
        <a-menu-item key="13">
          轴参数
        </a-menu-item>
        <a-menu-item key=14>
          自定义
        </a-menu-item>
        <a-menu-item key="15">
          自定义宏
        </a-menu-item>
        <a-menu-item key="16">
          刀具参数
        </a-menu-item>
      </a-sub-menu>
      <a-sub-menu key="sub3">
        <span slot="title"><a-icon type="setting" /><span>系统配置</span></span>
        <a-menu-item key="17">
          应用配置
        </a-menu-item>
        <a-menu-item key="18">
          用户配置
        </a-menu-item>
      </a-sub-menu>
      </a-menu>
    </a-layout-sider>
    <a-layout>
      <router-view/>
    </a-layout>
  </a-layout>
</template>

<script>
export default {
    methods: {
      onCollapse (collapsed, type) {
        console.log(collapsed, type)
        console.log('onCollapse')
      },
      onBreakpoint (broken) {
        console.log(broken)
        console.log('onBreakpoint')
        this.$router.push({name: 'firstPage'})
      },
      handleClick(e) {
        switch(e.key){
          case("1"):
            this.$router.push({name: 'firstPage'});
            break;
          case("2"):
            this.$router.push({name: 'WANPage'});
            break;
          case("3"):
            this.$router.push({name: '_4GPage'});
            break;
          case("4"):
            this.$router.push({name: 'WIFIPage'});
            break;
          case("5"):
            this.$router.push({name: 'LANPage'});
            break;
          case("6"):
            this.$router.push({name: 'joinRouter'});
            break;
          case("7"):
            this.$router.push({name: 'RS232Page'});
            break;
          case("8"):
            this.$router.push({name: 'RS485Page'});
            break;
          case("9"):
            this.$router.push({name: 'IOPage'});
            break;
          case("10"):
            this.$router.push({name: 'uploadPage'});
            break;
          case("11"):
            this.$router.push({name: 'CNCBase'});
            break;
          case("12"):
            this.$router.push({name: 'CNCFile'});
            break;
          case("13"):
            this.$router.push({name: 'CNCZhou'});
            break;
          case("14"):
            this.$router.push({name: 'CNCZDY'});
            break;
          case("15"):
            this.$router.push({name: 'CNCZDYH'});
            break;
          case("16"):
            this.$router.push({name: 'CNCDJ'});
            break;
          case("17"):
            this.$router.push({name: 'ApplicationPage'});
            break;
          case("18"):
            this.$router.push({name: 'userPage'});
            break;
        }
          
        
        
    },
  }
}
</script>

<style>
.components-layout{
  height: 100vh;
  width: 100%;
}
.components-logo {
  height: 10vh;
  width: 75%;
  background:url(../assets/logo.png);
  margin: 18px;
}
</style>