<template>
  <a-layout>
    <a-layout-header :style="{ background: '#fff', padding:'0 10px', height:'7vh'}" >
      <span class="header-text">设备配置</span>
      <a-button type="primary" size='small' icon="save" style="position:absolute;right:1%;top:2vh" @click="save">
         保存
      </a-button>
    </a-layout-header>
    <a-layout-content>
      <a-card title="自定义宏配置">
        <span class='form-text-upload'>用途:</span>
        <a-select default-value="自定义" style="width: 10%" @change="handleChange">
          <a-select-option value="自定义">
            自定义
          </a-select-option>
        </a-select>
        <span style="display: inline-block;height:32px;width:60%;margin-left:20%;">
          <span class='form-text-zdy'>批量导入:</span>
          <a-upload
          name="file"
          :multiple="true"
          action=""
          :headers="headers"
          @change="handleChange"
        >
          <a-button type="primary"> <a-icon type="upload" /> 选择文件 </a-button>
        </a-upload>
        </span>
        <br>
        <br>
        <span class='form-text-upload'>路径:</span>
        <a-input placeholder="1" style="width: 10%"/>
        <br/>
        <br>
        <span class='form-text-upload'>地址类型:</span>
        <a-select default-value="位" style="width: 10%" @change="handleChange">
          <a-select-option value="位">
            位
          </a-select-option>
        </a-select>
        <br/>
        <br>
        <span class='form-text-upload'>位地址/长度:</span>
        <a-input placeholder="0" style="width: 10%"/>
        <br>
        <br>
         <a-button icon="rollback" size='small' style="margin-left:4%;background-color:green;color:#FFF">
          返回
        </a-button>
      </a-card>
      <a-card title="自定义宏状态">
        <a-table :columns="columns" :data-source="tableData" bordered>
        </a-table>
      </a-card>
    </a-layout-content>
  </a-layout>
</template>

<script>
export default {
  data(){
    return {
      columns:[
        {
          dataIndex:'index',
          title:'序号'
        },
        {
          dataIndex:'path',
          title:'路径'
        },
        {
          dataIndex:'address',
          title:'地址'
        },
        {
          dataIndex:'addressType',
          title:'地址类型'
        },
        {
          dataIndex:'zhouType',
          title:'位地址/长度'
        },
        {
          dataIndex:'Tpye',
          title:'类型'
        }
      ],
      tableData:[]
    }
  },
  method:{
      save(){

      },
      handleChange(){

      }
  }
}
</script>

<style>
.header-text{
  font-size: 20px;
  font-weight: 600;
}
.form-text-zdy{
  display: inline-block;
  height: 32px;
  line-height: 32px;
  width: 8%;
  text-align: center;
}
.ant-card-head-title{
  font-weight: 800
}
</style>